import user1 from "./../image/user1.png";
import user2 from "./../image/user2.png";
import user3 from "./../image/user3.png";
import user4 from "./../image/user4.png";
import avatar from "./../image/author.JPG";
import success from "./../image/success.png";
import fail from "./../image/fail.png";
import bgSuccess from "./../image/greenBubbles.png";
import bgFail from "./../image/redBubbles.png";

let rerenderEntireTree = () => {}

let state = {
  authorInfo: {
    avatar: { avatar },
    fullName: " ",
    birthday: "12.06.1994",
    town: "",
    gender: "",
    age: "27",
    aboutSelf:
      ' !   ,      - :)          "  ",     1,5 .      ,       :)',
    ps: " ,       :)",
    pet: "",
  },

  newCommentText: "",
  newCommentName: "",

  commentsData: [
    {
      url: { user1 },
      name: " ",
      date: "08.01.2022",
      comment:
        " ,            .  KPI        -      ,    .  -   ,     ,   .    .",
    },
    {
      url: { user2 },
      name: " ",
      date: "08.01.2022",
      comment:
        "   ,   ,      .      ,      ,    .      .         .",
    },
    {
      url: { user3 },
      name: " ",
      date: "26.12.2021",
      comment:
        "    ilink  .      :    ,   ,   .        ,    ,  ,   .     .  ,      ilink.",
    },
    {
      url: { user4 },
      name: " ",
      date: "16.12.2021",
      comment:
        "       ,      HR-    .       HR-.   .  ,   .    .   ,   ,    .     .  , .",
    },
  ],

  currentMessage: {
    src: '',
    title: '',
    text: '',
    alt : '',
  },
  messageSuccess: {
    src: { success },
    title: '!',
    text: '      :)',
    alt : '',
    style: {
    'background': '#b5ffa2',
    'backgroundImage': `url(${ bgSuccess })`,
    }
  },
  messageFail: {
    src: { fail },
    title: '-  ...',
    text: '   .   !',
    alt : '',
    style: {
      'background':'#F64B3C',
      'color': 'white',
      'backgroundImage': `url(${ bgFail })`,
    }
  }
};

export const addMessage = () => {
  state.currentMessage = state.newCommentText && state.newCommentName ? state.messageSuccess : state.messageFail;
}

export const addComment = () => {
  if (state.newCommentText && state.newCommentName) {
    const newComment = {
      url: { user2 },
      name: state.newCommentName,
      date: "16.12.2021",
      comment: state.newCommentText,
    };
    state.commentsData.push(newComment);
    state.newCommentText = "";
    state.newCommentName = "";
  } 
  rerenderEntireTree(state);
};

export const updateNewCommentText = (newText) => {
  state.newCommentText = newText;
  rerenderEntireTree(state);
};

export const updateNewCommentName = (newText) => {
  state.newCommentName = newText;
  rerenderEntireTree(state);
};

export const subscribe = (observer) => {
  rerenderEntireTree = observer;
};

export default state;

window.state = state;
