import React from "react";
import s from "./SectionWelcome.module.scss";
import pet from "./../../../image/pet.png";
import gender from "./../../../image/female.png";

function SectionWelcome(props) {
  const keys = Object.keys(props.authorInfo.avatar);
  return (
    <section className={s.section}>
      <h1>   !</h1>
      <div className={s.wrapper}>
        <div className={s.wrapperImg}>
          <img src={props.authorInfo.avatar[keys[0]]} alt=" " />
        </div>
        <div className={s.authorInfo}>
          <div className={s.nameYear}>
            <h3>{props.authorInfo.fullName}</h3>
            <span>{props.authorInfo.birthday}</span>
          </div>
          <div className={s.townGenderYear}>
            <span>
              <b>: </b> {props.authorInfo.town}
            </span>
            <div className={s.gender}>
              <span>
                <b>: </b> {props.authorInfo.gender}
              </span>{" "}
              <img src={gender} alt="  " />{" "}
            </div>
            <span>
              <b>: </b> {props.authorInfo.age}
            </span>
          </div>
          <p className={s.aboutSelf}>
            <b> :</b> {props.authorInfo.aboutSelf}
          </p>
          <p className={s.ps}> WTF: {props.authorInfo.ps}</p>
          <p className={s.pet}>
            <img src={pet} alt="  " />
            <b> :</b> {props.authorInfo.pet}
          </p>
        </div>
      </div>
    </section>
  );
}

export default SectionWelcome;
